.Title {
  margin-top: 100px;
  margin-bottom: 20px;
  display: block;
  justify-content: center;
  background-color: #DAD6D7;
}

.SubTitle {
  position: relative;
  font-size: 1.5em;
  font-weight: bold;
  letter-spacing: 0.3em;
  margin: 20px;
}

.Button {
  margin-top: 20px;
  margin-left: 20px;
  margin-bottom: 40px;
}

.Link {
  margin-top: 20px;
  margin-left: 20px;
}

.MainArea{
    margin-top: auto;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: auto;
    background-color: white;
    display: flex;
    justify-content:space-between;
    align-items: center;
    flex-direction: row;
    min-height: 20%;   
    height: 260px; 
    position: relative;
    border-radius: 3px;
}

.MainArea:hover {
    border-color: white;
    box-shadow: 0 0 10px 0 white;
}

.TextArea{
    margin-left: 20px;
    margin-right: 20px;
    align-items: center;
    flex-direction: row;
    position: relative;
}

.ErrorMainArea{
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: auto;
    background-color: white;
    align-items: center;
    flex-direction: row;
    min-height: 60%;   
    height: 400px; 
    position: relative;
    border-radius: 3px;
}

.ErrorDetailArea{
    margin-top: 20px;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: auto;
    align-items: center; 
    min-height: 20%;   
}

.CardName{
    margin-top: 0;
    margin-bottom: 0;
    align-items: center;
    font-size: 1.2rem;
    color: rgb(65, 60, 60);
}
