.Mainarea {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    min-height: 75vh;
    color: #5d5d5b;
}

.Canvas {
    margin-top: 20px;
    margin-left: 100px;
    height: 70vh;
    width: 150vh;
    padding: 50px;
    border-radius: 10px;
    position: relative;
    text-align: center;
    background-color: aliceblue;
}

.CanvasConfig {
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 100px;
    height: 73vh;
    min-width: 15%;
    padding: 30px;
    border-radius: 10px;
    position: relative;
    text-align: center;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.BpmnProps {
    align-self: auto;
}

.BpmnDevelopers {
    align-self: auto;
}

.Title {
    margin-top: 100px;
    margin-bottom: 20px;
    margin-left: 100px;
    margin-right: 100px;
}

.Button {
    margin-top: 10px;
    margin-left: 100px;
    margin-right: 100px;
}
