.TitleAndSearch{
    margin-top: 40px;
    margin-bottom: 40px;
    display: flex;
    justify-content: space-between;
}

.Title { 
    margin-top: 40px;
    color: #edeede;
    font-weight: normal;
    font-size: 3rem;
    line-height: 15px;
    letter-spacing: 0.2em;
}

.Search { 
    border-radius: 10px;
    background-color:white;
    margin-left: 10vh;
    margin-top: 20px;
    margin-bottom: 10px;
    min-width: 50vh;
}

.Search:hover, .Search:focus{
    border-color: black;
    box-shadow: 0 0 10px 0 white;
}

.Background {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    margin-left: 100px;
    margin-right: 100px;
    min-height: 70vh;
    width: 150vh;
    border-radius: 10px;
    background-color: #5D5D5B;
}

.BlockCSS {
    overflow-y: scroll;
    margin-left: 40px;
    margin-right: 40px;
    margin-top: 50px;
    margin-bottom:50px;
    width: 80vh;
    height: 70vh;
    background-color: #C4C4C4;
    border-color: white;
    box-shadow: 0 0 5px 0 white;
}

.TaskTitle{
    font-size: 1.5em;
    font-weight: bold;
    letter-spacing: .3em;
    margin: 20px;
}


.Button {
  display: flex;
  margin-top: 20px;
  margin-left: 80px;
  margin-right: 20px;
  margin-bottom: 20px;
}

